<template >
  <div class="vsv-card">
    <div class="vsv-card-header">
       <div class="vsv-rest-brand"><router-link class="vsv-internal-link" :to="linkPath">{{ linkName }}</router-link>
       </div>
       <div class="vsv-rest-select">
            <label for="selectjob" class="custom-ck">
              <input type="checkbox" id="selectjob" name="selectjob" />
              <img class="vsv-chefs-cb" src="~assets/icns/ticks/tick2w.svg"/>
              <span>&nbsp;{{ select }}</span>
            </label>
       </div>
       <div class="vsv-rosettes">
       <img class="vsv-chefs-rest-stars" src="~assets/icns/stars/rosette_star@2x.png"/>
       <img class="vsv-chefs-rest-stars" src="~assets/icns/stars/rosette_star@2x.png"/>
       <img class="vsv-chefs-rest-stars" src="~assets/icns/stars/rosette_star@2x.png"/>
       <img class="vsv-chefs-rest-stars" src="~assets/icns/stars/rosette_star@2x.png"/>
       </div>
       <div class="vsv-city-location">{{location}}</div>
    </div>
    <div class="vsv-card-content">
      <div class="vsv-rest-images">
        <div class="vsv-rest-premium">
        </div>
        <div class="vsv-rest-logo"></div>
        <div class="vsv-rest-image"></div>
      </div>
      <div class="vsv-job-offer-title">
        <span>{{jobData[0]}} / {{jobData[1]}} / {{jobData[2]}} / {{jobData[3]}} / </span>
      </div>
      <div class="vsv-job-offer-description">
        <span> Lorem ipsum dolor, sit amet consectetur adipisicing elit. Iusto molestiae odio provident sapiente ipsa, tempora similique, doloribus fugiat repellat laboriosam quaerat tenetur magni minus dolore, alias earum error magnam illum? </span>
      </div>
      <div class="vsv-job-offer-type">
          <div class="vsv-job-opts">{{jobOpts[0]}}</div>
          <div class="vsv-job-opts">{{jobOpts[1]}}</div>
          <div class="vsv-job-opts">{{jobOpts[2]}}</div>
          <div class="vsv-job-opts">{{jobOpts[3]}}</div>
      </div>
      <hr>
      <div class="vsv-job-offer-footer">
        <div class="vsv-job-social"> 
        {{offerData[0]}}
        </div>
        <div class="vsv-job-social">
        {{offerData[1]}}
        <img class="vsv-chefs-card-links" src="~assets/icns/message_icn.svg"/>
        </div>
        <div class="vsv-job-social">
        {{offerData[2]}}
        <img class="vsv-chefs-card-links" src="~assets/icns/share_icn.svg"/>
        </div>
        <div class="vsv-job-social">
        {{offerData[3]}}
        <img class="vsv-chefs-card-links" src="~assets/icns/link_icn.svg"/>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'vsvCard',
    data() {
      return {
        element: 'vsv-card',
        restName: 'Pollen street social',
        select: 'select',
        location: 'London, UK',
        linkPath: '/rest',
        linkName: 'POLLEN STREET SOCIAL',
        jobData: [
          'Head Pastry Chef',
          'British',
          'London',
          '40.000 - 45.000',
        ],
        jobOpts: [
          'term: full-time',
          'hours: 6 shifts',
          'holidays: 28',
          'days off: 2',
        ],
        offerData: [
          'published on December 4, 2017',
          'message',
          'share',
          'links',
        ],
      };
    },
  };
</script>
<style lang="scss" scoped>
@import "./../assets/css/main";

    hr {
      color: $vsv-gray-secondary;
      margin: 5px 0 0 0;
    }
    .vsv-card {
      width: 100%;
      min-height: 400px;
      margin-bottom: 30px;
      text-transform: uppercase;
    }
    .vsv-card-content {
      min-height: inherit;
      padding: 20px 0 0 0;
      -webkit-border-radius: 6px;
      -moz-border-radius: 6px;
      border-radius: 6px;
      -webkit-box-shadow: 0px 0px 26px 0px rgba(158,156,158,1);
      -moz-box-shadow: 0px 0px 26px 0px rgba(158,156,158,1);
      box-shadow: 0px 0px 26px 0px rgba(158,156,158,1);
      z-index: 15;
      position: relative;
      top: -8px;
      background: $vsv-white;
    }
    .vsv-card-header {
      display: flex;
      flex-direction: row;
      font-size: 14px;
      line-height: 25px;
    }
    .vsv-rest-brand{
      background:$vsv-dark-primary;
      color: $vsv-white;
      text-align: center;
      padding:5px 25px;
      height: 25px;
      flex:2;
      -webkit-border-top-left-radius: 8px;
      -moz-border-radius-topleft: 8px;
      border-top-left-radius: 8px;
      border-bottom: 8px solid $vsv-dark-primary;
    }
    .vsv-rest-select{
      background: $vsv-blue-primary;
      color: $vsv-white;
      text-align: center;
      padding:5px 10px;
      height: 25px;
      flex:1;
      -webkit-border-top-right-radius: 8px;
      -moz-border-radius-topright: 8px;
      border-top-right-radius: 8px;
    }
    .vsv-rosettes {
      flex:1;
      text-align: left   ;
      padding:5px 8px;
      height: 25px;
    }
    .vsv-city-location{
      flex: 1;
      text-align: center;
      padding:5px;
      height: 25px;
      text-align: right;
      font-size: 12px;
    }
    .vsv-rest-images {
      display: flex;
      height: 200px;
      margin-bottom: 15px;
      padding-right: 20px;
    }
    .vsv-rest-logo {
      flex:1;
      border: 1px solid $vsv-gray;
      margin-right: 10px;
      background-image: url(~assets/img/job_offers/1/1_logo.jpg);
      background-size: cover;

    }
    .vsv-rest-image {
      flex:2;
      border: 1px solid $vsv-gray;
      margin-left: 10px;
      background-image: url(~assets/img/job_offers/1/1_rest.jpg);
      background-size: cover;
    }
    .vsv-rest-premium {
      width: 30px;
      border:1px solid $vsv-gray;
      visibility: hidden;
    }
    .vsv-rest-premium > span {
      transform: rotate(90deg);
    }
    .vsv-job-offer-title {
      text-align: left;
      text-transform: capitalize;
      color: $vsv-blue-primary;
      font-weight: 900;
      padding-left: 30px;
      margin-bottom: 15px;
    }
    .vsv-job-offer-description {
      padding:0 20px 0 30px;
      text-transform: none;
      text-align: left;
      margin-bottom: 20px;
    }
    .vsv-job-offer-description::after{
     content: "+Read More";
      color: $vsv-gray;
    } 
    .vsv-job-offer-type {
      display: flex;
      padding-left: 30px;
      text-transform: uppercase;
      font-size: 12px;
      text-align: left;
      margin-bottom: 20px;
    }
    .vsv-job-offer-type > *{
      flex:1;
    }
    .vsv-job-offer-footer {
      display: flex;
      line-height: 30px;
      text-transform: uppercase;
      font-size: 12px;
      text-align: right;
      padding:0 20px 0 30px;
    }
    .vsv-job-offer-footer:first-child {
      flex:1;
    }
    .vsv-job-offer-footer div:not(:first-child) {
      flex: 1;
    }
    #selectjob {
      display: none;
    }
    .vsv-chefs-cb {
        width: 16px;
  vertical-align: middle;
  position: relative;
  top: -2px;
    }
  .vsv-chefs-rest-stars {
      width: 14px;
  }
  .vsv-jobs-social {
    vertical-align: middle;
  }
  .vsv-chefs-card-links {
    height: 14px;
    padding-left: 4px;
    position: relative;
    top: 2px;
  }
</style>



